import React, { ButtonHTMLAttributes } from 'react';
import { cva, type VariantProps } from 'class-variance-authority';

const buttonVariants = cva(
  "inline-flex items-center justify-center rounded-md text-sm font-medium transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:opacity-50 disabled:pointer-events-none",
  {
    variants: {
      variant: {
        default: "bg-primary text-primary-foreground hover:bg-primary/90 dark-cyber:bg-dark-cyber-primary dark-cyber:text-dark-cyber-bg dark-cyber:hover:bg-dark-cyber-accent",
        destructive: "bg-destructive text-destructive-foreground hover:bg-destructive/90 dark-cyber:bg-dark-cyber-destructive dark-cyber:hover:bg-dark-cyber-destructive/90",
        outline: "border border-input hover:bg-accent hover:text-accent-foreground dark-cyber:border-dark-cyber-border dark-cyber:hover:bg-dark-cyber-muted",
        secondary: "bg-secondary text-secondary-foreground hover:bg-secondary/80 dark-cyber:bg-dark-cyber-secondary dark-cyber:text-dark-cyber-text dark-cyber:hover:bg-dark-cyber-secondary/80",
        ghost: "hover:bg-accent hover:text-accent-foreground dark-cyber:hover:bg-dark-cyber-muted/50 dark-cyber:hover:text-dark-cyber-text",
        link: "underline-offset-4 hover:underline text-primary dark-cyber:text-dark-cyber-primary",
        cyber: "relative overflow-hidden border border-dark-cyber-accent bg-dark-cyber-card text-dark-cyber-accent hover:text-dark-cyber-bg hover:bg-dark-cyber-accent transition-colors duration-300 shadow-cyber-glow",
      },
      size: {
        default: "h-10 py-2 px-4",
        sm: "h-9 px-3 rounded-md",
        lg: "h-11 px-8 rounded-md",
        icon: "h-10 w-10",
      },
    },
    defaultVariants: {
      variant: "default",
      size: "default",
    },
  }
);

export interface ButtonProps
  extends ButtonHTMLAttributes<HTMLButtonElement>,
    VariantProps<typeof buttonVariants> {
  isLoading?: boolean;
}

const Button = React.forwardRef<HTMLButtonElement, ButtonProps>(
  ({ className, variant, size, isLoading = false, children, ...props }, ref) => {
    return (
      <button
        className={buttonVariants({ variant, size, className })}
        ref={ref}
        disabled={isLoading || props.disabled}
        {...props}
      >
        {isLoading && (
          <svg
            className="animate-spin -ml-1 mr-2 h-4 w-4 text-current"
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
          >
            <circle
              className="opacity-25"
              cx="12"
              cy="12"
              r="10"
              stroke="currentColor"
              strokeWidth="4"
            ></circle>
            <path
              className="opacity-75"
              fill="currentColor"
              d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
            ></path>
          </svg>
        )}
        {children}
      </button>
    );
  }
);

Button.displayName = "Button";

export { Button, buttonVariants };