"use client";

import { useAwsCredentials } from "@/hooks/useAwsCredentials";
import { useRouter } from "next/navigation";
import { useState, useEffect } from "react";
import AlertsCard from "@/components/dashboard/AlertsCard";
import { motion } from "framer-motion";
import { useSession } from "next-auth/react";
import { Button } from "@/components/ui/button";
import { ArrowRightIcon, CloudIcon, ShieldExclamationIcon, ServerStackIcon } from "@heroicons/react/24/outline";
import DashboardCard from "@/components/dashboard/DashboardCard";

// Interface para os dados do dashboard
interface DashboardData {
  resources: {
    ec2: number;
    s3: number;
    rds: number;
    lambda: number;
    cloudfront: number;
  };
  security: {
    score: number;
    issues: {
      critical: number;
      high: number;
      medium: number;
      low: number;
    };
    highRiskUsers: number;
    mediumRiskUsers: number;
    lowRiskUsers: number;
  };
  compliance: {
    pci: number;
    hipaa: number;
    gdpr: number;
    nist: number;
  };
}

export default function DashboardPage() {
  const { credentials, selectedCredential, loading: credentialsLoading } = useAwsCredentials();
  const router = useRouter();
  const { data: session, status } = useSession();
  const [activeTab, setActiveTab] = useState('seguranca');
  const [dashboardData, setDashboardData] = useState<DashboardData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [lastUpdated, setLastUpdated] = useState<Date | null>(null);
  const [isDarkCyber, setIsDarkCyber] = useState(false);

  // Verificar o status do tema dark-cyber
  useEffect(() => {
    const checkTheme = () => {
      if (typeof window !== 'undefined') {
        setIsDarkCyber(document.body.classList.contains('dark-cyber'));
      }
    };
    
    checkTheme();
    
    // Observar mudanças no tema
    const observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        if (mutation.attributeName === 'class') {
          checkTheme();
        }
      });
    });
    
    if (typeof window !== 'undefined') {
      observer.observe(document.body, { attributes: true });
    }
    
    return () => observer.disconnect();
  }, []);

  // Redirecionar se não autenticado
  useEffect(() => {
    if (status === "unauthenticated") {
      router.push("/login");
    }
  }, [status, router]);

  // Carregar dados do dashboard quando a credencial mudar
  useEffect(() => {
    async function fetchDashboardData() {
      if (!selectedCredential) {
        setLoading(false);
        return;
      }

      setLoading(true);
      setError(null);

      try {
        const response = await fetch(`/api/aws/dashboard?credentialId=${selectedCredential.id}`);
        
        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.error || `Erro ${response.status}`);
        }

        const data = await response.json();
        setDashboardData(data);
        setLastUpdated(new Date());
      } catch (err) {
        console.error('Erro ao carregar dados do dashboard:', err);
        setError(err instanceof Error ? err.message : 'Erro desconhecido');
      } finally {
        setLoading(false);
      }
    }

    fetchDashboardData();
  }, [selectedCredential]);

  // Função para atualizar dados
  const handleRefresh = async () => {
    if (!selectedCredential) return;
    
    setLoading(true);
    setError(null);

    try {
      const response = await fetch(`/api/aws/dashboard?credentialId=${selectedCredential.id}`);
      
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || `Erro ${response.status}`);
      }

      const data = await response.json();
      setDashboardData(data);
      setLastUpdated(new Date());
    } catch (err) {
      console.error('Erro ao atualizar dados do dashboard:', err);
      setError(err instanceof Error ? err.message : 'Erro desconhecido');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      <motion.div
        className="mb-6"
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.3 }}
      >
        <div className="flex justify-between items-center">
          <div>
            <h1 className="text-3xl font-bold text-gray-900 dark-cyber:text-dark-cyber-text">Dashboard</h1>
            <p className="mt-2 text-gray-600 dark-cyber:text-dark-cyber-text/70">
              Bem-vindo, {session?.user?.name || "Usuário"}. Monitore e gerencie seus recursos AWS.
            </p>
          </div>
          {selectedCredential && (
            <div className="bg-white shadow rounded-lg px-4 py-2 flex items-center">
              <div className="h-8 w-8 bg-indigo-100 rounded-full flex items-center justify-center text-indigo-800 font-bold mr-3">
                {selectedCredential.name.charAt(0).toUpperCase()}
              </div>
              <div>
                <p className="text-sm font-medium text-gray-900">{selectedCredential.name}</p>
                <p className="text-xs text-gray-500">{selectedCredential.region}</p>
              </div>
            </div>
          )}
        </div>
      </motion.div>

      {credentialsLoading || loading ? (
        <div className="flex justify-center py-12">
          <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-500"></div>
        </div>
      ) : error ? (
        <motion.div
          className="bg-white rounded-lg shadow-md p-6"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.3 }}
        >
          <div className="p-4 bg-red-50 rounded-md mb-4">
            <p className="text-red-700">{error}</p>
          </div>
          <button
            onClick={handleRefresh}
            className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600 disabled:opacity-50"
          >
            Tentar novamente
          </button>
        </motion.div>
      ) : credentials.length === 0 ? (
        <motion.div
          className="bg-white rounded-lg shadow-md p-6 text-center"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.3 }}
        >
          <h2 className="text-xl font-semibold text-gray-800 dark-cyber:text-dark-cyber-text mb-4">
            Adicione suas credenciais AWS
          </h2>
          <p className="text-gray-600 dark-cyber:text-dark-cyber-text/80 mb-4">
            Para começar a monitorar seus recursos AWS, adicione suas credenciais AWS.
          </p>
          <Button
            onClick={() => router.push("/dashboard/credentials")}
            className={isDarkCyber ? 'cyber-btn' : ''}
          >
            Adicionar Credenciais
          </Button>
        </motion.div>
      ) : (
        <>
          {/* Header com botão de atualização e última atualização */}
          <div className="flex justify-between items-center mb-4">
            <div className="text-sm text-gray-500">
              {lastUpdated && (
                <span>Última atualização: {lastUpdated.toLocaleTimeString()}</span>
              )}
            </div>
            <button
              onClick={handleRefresh}
              disabled={loading}
              className="flex items-center px-3 py-2 bg-gray-100 text-gray-600 rounded-md hover:bg-gray-200 disabled:opacity-50"
            >
              <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
              </svg>
              Atualizar
            </button>
          </div>

          {/* Abas */}
          <div className="bg-white rounded-t-lg shadow-sm mb-0 border-b border-gray-200">
            <nav className="flex space-x-4 px-6" aria-label="Tabs">
              <button
                onClick={() => setActiveTab('seguranca')}
                className={`py-4 px-2 text-sm font-medium border-b-2 ${
                  activeTab === 'seguranca'
                    ? 'border-indigo-500 text-indigo-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                Segurança
              </button>
              <button
                onClick={() => setActiveTab('recursos')}
                className={`py-4 px-2 text-sm font-medium border-b-2 ${
                  activeTab === 'recursos'
                    ? 'border-indigo-500 text-indigo-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                Recursos
              </button>
              <button
                onClick={() => setActiveTab('compliance')}
                className={`py-4 px-2 text-sm font-medium border-b-2 ${
                  activeTab === 'compliance'
                    ? 'border-indigo-500 text-indigo-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                Compliance
              </button>
            </nav>
          </div>

          {/* Conteúdo das abas */}
          <motion.div
            key={activeTab}
            className="bg-white rounded-b-lg shadow-md p-6"
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.2 }}
          >
            {activeTab === 'seguranca' && dashboardData && (
              <div className="space-y-6">
                {/* Score de Segurança */}
                <div className="border-b border-gray-200 pb-6">
                  <h3 className="text-lg font-medium text-gray-900 mb-4">Score de Segurança</h3>
                  <div className="flex items-center">
                    <div className="relative h-32 w-32">
                      <div className="absolute inset-0 flex items-center justify-center flex-col">
                        <span className="text-3xl font-bold text-gray-900">{dashboardData.security.score}</span>
                        <span className={`text-sm font-medium text-gray-600`}>
                          /100
                        </span>
                      </div>
                      {/* Círculo para representar o score */}
                      <div 
                        className="h-full w-full rounded-full bg-gray-100"
                        style={{
                          background: `conic-gradient(#4f46e5 ${dashboardData.security.score * 3.6}deg, #e5e7eb 0deg)`
                        }}
                      ></div>
                      <div className="absolute inset-2 bg-white rounded-full"></div>
                    </div>
                    <div className="ml-8 flex-1">
                      <h4 className="text-sm font-medium text-gray-500 mb-3">Problemas de Segurança</h4>
                      <div className="grid grid-cols-3 gap-4">
                        <div className="bg-red-50 p-3 rounded-lg">
                          <p className="text-xs font-medium text-red-800">Alta Severidade</p>
                          <p className="text-2xl font-bold text-red-600">
                            {dashboardData.security.issues.critical + dashboardData.security.issues.high}
                          </p>
                        </div>
                        <div className="bg-yellow-50 p-3 rounded-lg">
                          <p className="text-xs font-medium text-yellow-800">Média Severidade</p>
                          <p className="text-2xl font-bold text-yellow-600">{dashboardData.security.issues.medium}</p>
                        </div>
                        <div className="bg-blue-50 p-3 rounded-lg">
                          <p className="text-xs font-medium text-blue-800">Baixa Severidade</p>
                          <p className="text-2xl font-bold text-blue-600">{dashboardData.security.issues.low}</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Alertas de Segurança */}
                <div>
                  <h3 className="text-lg font-medium text-gray-900 mb-4">Alertas Recentes</h3>
                  <AlertsCard />
                </div>
              </div>
            )}

            {activeTab === 'recursos' && dashboardData && (
              <div className="space-y-6">
                <h3 className="text-lg font-medium text-gray-900 mb-4">Recursos na AWS</h3>
                
                {/* Gráfico de Recursos */}
                <div className="grid grid-cols-5 gap-4 mb-6">
                  <div className="bg-blue-50 p-4 rounded-lg text-center">
                    <p className="text-sm font-medium text-blue-800">EC2</p>
                    <p className="text-2xl font-bold text-blue-700">{dashboardData.resources.ec2}</p>
                  </div>
                  <div className="bg-green-50 p-4 rounded-lg text-center">
                    <p className="text-sm font-medium text-green-800">S3</p>
                    <p className="text-2xl font-bold text-green-700">{dashboardData.resources.s3}</p>
                  </div>
                  <div className="bg-purple-50 p-4 rounded-lg text-center">
                    <p className="text-sm font-medium text-purple-800">RDS</p>
                    <p className="text-2xl font-bold text-purple-700">{dashboardData.resources.rds}</p>
                  </div>
                  <div className="bg-yellow-50 p-4 rounded-lg text-center">
                    <p className="text-sm font-medium text-yellow-800">Lambda</p>
                    <p className="text-2xl font-bold text-yellow-700">{dashboardData.resources.lambda}</p>
                  </div>
                  <div className="bg-indigo-50 p-4 rounded-lg text-center">
                    <p className="text-sm font-medium text-indigo-800">CloudFront</p>
                    <p className="text-2xl font-bold text-indigo-700">{dashboardData.resources.cloudfront}</p>
                  </div>
                </div>
                
                {/* Distribuição de recursos */}
                <div className="bg-white border border-gray-200 p-4 rounded-lg">
                  <h4 className="text-sm font-medium text-gray-700 mb-4">Distribuição de Recursos</h4>
                  <div className="relative pt-1">
                    <div className="flex h-6 overflow-hidden text-xs rounded-full bg-gray-100">
                      <div
                        style={{ width: `${Math.round((dashboardData.resources.ec2 / getTotalResources(dashboardData)) * 100)}%` }}
                        className="flex flex-col text-center whitespace-nowrap text-white justify-center bg-blue-500"
                      ></div>
                      <div
                        style={{ width: `${Math.round((dashboardData.resources.s3 / getTotalResources(dashboardData)) * 100)}%` }}
                        className="flex flex-col text-center whitespace-nowrap text-white justify-center bg-green-500"
                      ></div>
                      <div
                        style={{ width: `${Math.round((dashboardData.resources.rds / getTotalResources(dashboardData)) * 100)}%` }}
                        className="flex flex-col text-center whitespace-nowrap text-white justify-center bg-purple-500"
                      ></div>
                      <div
                        style={{ width: `${Math.round((dashboardData.resources.lambda / getTotalResources(dashboardData)) * 100)}%` }}
                        className="flex flex-col text-center whitespace-nowrap text-white justify-center bg-yellow-500"
                      ></div>
                      <div
                        style={{ width: `${Math.round((dashboardData.resources.cloudfront / getTotalResources(dashboardData)) * 100)}%` }}
                        className="flex flex-col text-center whitespace-nowrap text-white justify-center bg-indigo-500"
                      ></div>
                    </div>
                  </div>
                  <div className="flex flex-wrap gap-4 mt-3">
                    <div className="flex items-center text-xs">
                      <span className="w-3 h-3 inline-block mr-1 bg-blue-500 rounded-sm"></span>
                      <span>EC2</span>
                    </div>
                    <div className="flex items-center text-xs">
                      <span className="w-3 h-3 inline-block mr-1 bg-green-500 rounded-sm"></span>
                      <span>S3</span>
                    </div>
                    <div className="flex items-center text-xs">
                      <span className="w-3 h-3 inline-block mr-1 bg-purple-500 rounded-sm"></span>
                      <span>RDS</span>
                    </div>
                    <div className="flex items-center text-xs">
                      <span className="w-3 h-3 inline-block mr-1 bg-yellow-500 rounded-sm"></span>
                      <span>Lambda</span>
                    </div>
                    <div className="flex items-center text-xs">
                      <span className="w-3 h-3 inline-block mr-1 bg-indigo-500 rounded-sm"></span>
                      <span>CloudFront</span>
                    </div>
                  </div>
                </div>
              </div>
            )}

            {activeTab === 'compliance' && dashboardData && (
              <div className="space-y-6">
                <h3 className="text-lg font-medium text-gray-900 mb-4">Conformidade e Certificações</h3>
                
                {/* Gráficos de Compliance */}
                <div className="grid grid-cols-4 gap-4 mb-6">
                  <div className="bg-white border border-gray-200 p-4 rounded-lg">
                    <h4 className="text-sm font-medium text-gray-700 mb-2">PCI DSS</h4>
                    <div className="flex items-end">
                      <div className="relative h-3 w-full bg-gray-100 rounded-full overflow-hidden">
                        <div className="absolute top-0 left-0 h-full bg-indigo-500" style={{ width: `${dashboardData.compliance.pci}%` }}></div>
                      </div>
                      <span className="text-sm font-medium text-gray-900 ml-2">{dashboardData.compliance.pci}%</span>
                    </div>
                  </div>
                  <div className="bg-white border border-gray-200 p-4 rounded-lg">
                    <h4 className="text-sm font-medium text-gray-700 mb-2">HIPAA</h4>
                    <div className="flex items-end">
                      <div className="relative h-3 w-full bg-gray-100 rounded-full overflow-hidden">
                        <div className="absolute top-0 left-0 h-full bg-indigo-500" style={{ width: `${dashboardData.compliance.hipaa}%` }}></div>
                      </div>
                      <span className="text-sm font-medium text-gray-900 ml-2">{dashboardData.compliance.hipaa}%</span>
                    </div>
                  </div>
                  <div className="bg-white border border-gray-200 p-4 rounded-lg">
                    <h4 className="text-sm font-medium text-gray-700 mb-2">GDPR</h4>
                    <div className="flex items-end">
                      <div className="relative h-3 w-full bg-gray-100 rounded-full overflow-hidden">
                        <div className="absolute top-0 left-0 h-full bg-indigo-500" style={{ width: `${dashboardData.compliance.gdpr}%` }}></div>
                      </div>
                      <span className="text-sm font-medium text-gray-900 ml-2">{dashboardData.compliance.gdpr}%</span>
                    </div>
                  </div>
                  <div className="bg-white border border-gray-200 p-4 rounded-lg">
                    <h4 className="text-sm font-medium text-gray-700 mb-2">NIST</h4>
                    <div className="flex items-end">
                      <div className="relative h-3 w-full bg-gray-100 rounded-full overflow-hidden">
                        <div className="absolute top-0 left-0 h-full bg-indigo-500" style={{ width: `${dashboardData.compliance.nist}%` }}></div>
                      </div>
                      <span className="text-sm font-medium text-gray-900 ml-2">{dashboardData.compliance.nist}%</span>
                    </div>
                  </div>
                </div>
                
                {/* Recomendações de Compliance */}
                <div className="bg-white border border-gray-200 p-4 rounded-lg">
                  <h4 className="text-sm font-medium text-gray-900 mb-3">Recomendações para melhorar conformidade</h4>
                  <ul className="space-y-2">
                    {dashboardData.security.highRiskUsers > 0 && (
                      <li className="flex items-start">
                        <span className="flex-shrink-0 h-5 w-5 bg-red-100 text-red-500 rounded-full flex items-center justify-center mr-2">
                          <svg xmlns="http://www.w3.org/2000/svg" className="h-3 w-3" viewBox="0 0 20 20" fill="currentColor">
                            <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                          </svg>
                        </span>
                        <span className="text-sm text-gray-700">Habilitar MFA para {dashboardData.security.highRiskUsers} usuários IAM de alto risco</span>
                      </li>
                    )}
                    {(dashboardData.security.issues.high + dashboardData.security.issues.critical) > 0 && (
                      <li className="flex items-start">
                        <span className="flex-shrink-0 h-5 w-5 bg-red-100 text-red-500 rounded-full flex items-center justify-center mr-2">
                          <svg xmlns="http://www.w3.org/2000/svg" className="h-3 w-3" viewBox="0 0 20 20" fill="currentColor">
                            <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                          </svg>
                        </span>
                        <span className="text-sm text-gray-700">Resolver {dashboardData.security.issues.high + dashboardData.security.issues.critical} problemas de segurança de alta severidade</span>
                      </li>
                    )}
                    <li className="flex items-start">
                      <span className="flex-shrink-0 h-5 w-5 bg-yellow-100 text-yellow-500 rounded-full flex items-center justify-center mr-2">
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-3 w-3" viewBox="0 0 20 20" fill="currentColor">
                          <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
                        </svg>
                      </span>
                      <span className="text-sm text-gray-700">Implementar política de rotação de chaves de acesso</span>
                    </li>
                  </ul>
                </div>
              </div>
            )}
          </motion.div>
        </>
      )}
    </div>
  );
}

// Função auxiliar para calcular o total de recursos
function getTotalResources(data: DashboardData) {
  return data.resources.ec2 + 
         data.resources.s3 + 
         data.resources.rds + 
         data.resources.lambda + 
         data.resources.cloudfront || 1; // Evitar divisão por zero
} 